.popup{
    position:fixed;
    top: 0;
    left :0;
    width: 100%;
    height: 100vh;
    /* background-color:  rgba(0,0,0,0.2); */
    /* border: red 5px solid; */
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.pop-inner{
    position: relative;
    padding:100px 32px 32px 32px;
    width: 100%;
    height: 80%;
    max-width:640px;
/* From https://css.glass */
background: rgba(16, 19, 22, 0.04);
border-radius: 16px;
box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(14px);
-webkit-backdrop-filter: blur(14px);
border: 1px solid #fff;
    border-radius: 50px;
}
.pop-inner h3,p{
    color: #fff;
}
.popup-inner .closebtn{
    position:absolute;
    top: 16px;
    right: 16px;
}
@media screen and (max-width:900px) and (min-device-width:300px) {
    .popup{
        height:90%;
        width:80%;
        align-items: center;
        /* justify-content: center; */
        margin:10%;
        /* From https://css.glass */
    }
    .pop-inner{
        display: flex;
        flex-direction: column;
        /* min-height: 80%; */
        min-height: fit-content;
        padding-top:90px;
        font-size: 85%;
        /* word-wrap: break-word; */
    }
}