body{
  background-color:#101316 ;
  color:#fff;
  font-family: 'Red Hat Display', sans-serif;
}
.Home{
display: flex;  
/* border: green 5px solid;  */
margin: 0 10%;
justify-content: center;
}
.t_box{
    display: flex;
    height: 90vh;
}
.left_box{
    /* border: red 5px solid;    */
    width: 60%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.fix_text{
    /* border: blue 5px solid; */
    width: 70%;  
    margin-left: 90px;
    display: flex;
    justify-content: center; 
}
button{
    background-color: #1BA3C5;
    border: none;
    width: 100px;
    cursor: pointer;
    border-radius: 10px;
    height: 30px;
}
.bio{
    margin-top: 20px;
}
button:hover{
    background-color: #34525a;
}
.right_box{
    /* border: red 5px solid;    */
    width: 50%;
    align-items: center;
    }
.icon_box{
    padding:0 10px;
    font-size: 22px;
}
.icn{
    padding-top: 100px;
    margin: 10px;
}
.icon_box a{
    color: #ffffff;
}
.fix_text{
    font-size: 30px;
}
.title_3{
    color: #1BA3C5;
    font-size: 28px;
}
.text{
    font-size: 20px;
    font-family:monospace;
    color: #FF3A16;
    font-weight: 700;
}
.pop-inner h3,p{
    color: #000;
    z-index: 99;
}
address a{
    font-size: 25px;
    color: #fff;
}
@media screen and (max-width:900px) {
    .Home{
        flex-direction: column;
        margin: 0;
        height: 100%;
    }
    .left_box{
        width: 100%;
        height:60vh;
        align-items: center;
    }
    .right_box{
        width:100%;
        align-items: unset;
        height:30vh;
        justify-content: center;
    }
    .fix_text{
        width: 50%;
        margin: 0;
    }
    .icn{
        padding-top: 30px;
    }
        /* .t_box{
            height:0vh;
        } */
}
/* .Home{
    background-color:  rgba(0,0,0,0.2);
} */
.popup{
    position:fixed;
    top: 0;
    left :0;
    width: 100%;
    height: 100vh;
    /* background-color:  rgba(0,0,0,0.2); */
    /* border: red 5px solid; */
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.pop-inner{
    position: relative;
    padding:100px 32px 32px 32px;
    width: 100%;
    height: 80%;
    max-width:640px;
/* From https://css.glass */
background: rgba(16, 19, 22, 0.04);
border-radius: 16px;
box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
backdrop-filter: blur(14px);
-webkit-backdrop-filter: blur(14px);
border: 1px solid #fff;
    border-radius: 50px;
}
.pop-inner h3,p{
    color: #fff;
}
.popup-inner .closebtn{
    position:absolute;
    top: 16px;
    right: 16px;
}
@media screen and (max-width:900px) and (min-device-width:300px) {
    .popup{
        height:90%;
        width:80%;
        align-items: center;
        /* justify-content: center; */
        margin:10%;
        /* From https://css.glass */
    }
    .pop-inner{
        display: flex;
        flex-direction: column;
        /* min-height: 80%; */
        min-height: -webkit-fit-content;
        min-height: -moz-fit-content;
        min-height: fit-content;
        padding-top:90px;
        font-size: 85%;
        /* word-wrap: break-word; */
    }
}
